export function GlibifyLogo({ className = "" }: { className?: string }) {
  return (
    <div className={className}>
      <svg viewBox="0 0 100 100" fill="none" xmlns="http://www.w3.org/2000/svg" className="w-full h-full">
        {/* Blue background circle */}
        <rect width="100" height="100" rx="20" fill="#0000FF" />

        {/* White G shape in speech bubble */}
        <g transform="translate(50, 50)">
          {/* Speech bubble pointer */}
          <path d="M 15 20 L 20 28 L 10 25 Z" fill="white" />

          {/* G letter */}
          <circle cx="0" cy="-2" r="16" fill="none" stroke="white" strokeWidth="2.5" />
          <line x1="0" y1="0" x2="12" y2="0" stroke="white" strokeWidth="2.5" strokeLinecap="round" />
          <line x1="12" y1="0" x2="12" y2="8" stroke="white" strokeWidth="2.5" strokeLinecap="round" />
          <line x1="12" y1="8" x2="2" y2="8" stroke="white" strokeWidth="2.5" strokeLinecap="round" />
        </g>
      </svg>
    </div>
  )
}
